import React from "react";
import { Button, Alert } from "@mui/material";
import { useNavigate } from "react-router-dom";
import { createNewUser } from "./database";

export default function Login() {
    const [value, setValue] = React.useState("");
    const [password, setPassword] = React.useState("");
    const [emailError, setEmailError] = React.useState("");
    const [name, setName] = React.useState("");
    const [lastName, setLastName] = React.useState("");
    const [firstName, setFirstName] = React.useState("");
    const [passwordError, setPasswordError] = React.useState("");
    const [lastNameError, setLastNameError] = React.useState("");
    const [firstNameError, setFirstNameError] = React.useState("");

    const navigate = useNavigate();
    const handleChange = (e) => {
        setValue(e.target.value);
    };
    const handleLastNameChange = (e) => {
        setLastName(e.target.value);
    };
    const handleFirstNameChange = (e) => {
        setFirstName(e.target.value);
    };
    const handlePasswordChange = (e) => {
        setPassword(e.target.value);
    };
    var delayInMilliseconds = 5000;

    const handleClick = () => {
        let error = false;
        const regex =
            /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
        if (regex.test(value) == false) {
            setEmailError("Not A valid Email");
            setTimeout(function() {
                setEmailError("");
            }, delayInMilliseconds);
            error = true;
        }
        if (firstName.length <= 0) {
            setFirstNameError("FirstName should be atleast 1 letter");
            setTimeout(function() {
                setFirstNameError("");
            }, delayInMilliseconds);
            error = true;
        }
    if(lastName.length <= 0){
        setLastNameError("LastName should be atleast 1 letter")
        setTimeout(function() {
            setLastNameError("");
        }, delayInMilliseconds);
        error = true;
        
    }
    if (password.length < 8) {
        setPasswordError("password should be atleast 8 letters");

        setTimeout(function() {
            setPasswordError("");
        }, delayInMilliseconds);
        error = true;
    }

    if (!error) {
        try {
            createNewUser({
                firstName,
                lastName,
                email: value,
                password,
            });
            
            navigate("/");
        } catch (e) {
            setPasswordError("User already exists");
        }
    }
};
return (
    <div className="Pag">
        <h1>SignUp</h1>
        <input
            type="text"
            name="text"
            onChange={handleFirstNameChange}
            value={firstName}
        />
        <input
            type="text"
            name="text"
            onChange={handleLastNameChange}
            value={lastName}
        />
        <input
            type="email"
            name="text"
            onChange={handleChange}
            value={value}
        />
        <input
            type="password"
            name="password"
            onChange={handlePasswordChange}
            value={password}
        />
        
            {[emailError,passwordError,lastNameError,firstNameError].map((Error,index) => (
                <div className="Error">
                    {Error ? <Alert severity='error' key={index}>{Error}</Alert> : null}
                </div>
        ))}
        <Button variant="contained" href={name} onClick={handleClick}>
            Submit
        </Button>
    </div>
);
}
