
function getDatabase() {
    return JSON.parse(window.localStorage.getItem("database") || "[]");
}

function setDatabase(users) {
    window.localStorage.setItem("database", JSON.stringify(users));
}

export function createNewUser(user) {
    const users = getDatabase();
    if (emailAlreadyExists(users, user.email))
        throw Error("Email already exists");

    users.push(user);
    setDatabase(users);
}

export function login(email, password) {
    const users = getDatabase();
    console.log(users);
    const match = users.find(user => user.email === email);
    if (match)
        setCurrentUser(match);
    console.log(match);
    return match && match.password === password;
}

export function setCurrentUser(user) {
    window.localStorage.setItem("user", JSON.stringify(user));
}

function emailAlreadyExists(users, email) {
    return users.find(user => user.email === email);
}

export function logout(){
    window.localStorage.removeItem('user');
}

export function currentUser(){
    const user = window.localStorage.getItem("user");
    console.log(user);
    if (user) return JSON.parse(user);
    else return null;
}
