import Paper from "@mui/material/Paper";
import React from "react";
import { useParams } from "react-router-dom";
import Button from '@mui/material/Button';

export default function P() {
  const [value,setValue] = React.useState('');
  const { id } = useParams();

  const storageId = "item_" + id;
    const handleShare = () => {
        window.localStorage.setItem('Share',value)
    }
  React.useEffect(() => {
      setValue(window.localStorage.getItem(storageId));
  }, [id])

  const handleChange = (e) => {
    setValue(e.target.value)
  }

  const handleClick = () => {
    window.localStorage.setItem(storageId, value)
  }
  
  return (
    <div>
        <textarea maxlength='500' value={value} onChange={handleChange}></textarea>
        <r>{JSON.stringify(value !== null ? 500 - value.length : 500) + ' letters'}</r>
      <Button variant='contained' href='/' className='doonnee'onClick={handleClick}>Done</Button>
      </div>
  );
}
