import React from "react";
import { Button,Alert } from "@mui/material";
import { useNavigate } from "react-router-dom";
import { login } from "./database";

export default function Login() {
    const [value, setValue] = React.useState("");
    const [password, setPassword] = React.useState("");
    const [emailError, setEmailError] = React.useState("");
    const [name, setName] = React.useState("");
    const [passwordError, setPasswordError] = React.useState("");

    const navigate = useNavigate();
    const handleChange = (e) => {
        setValue(e.target.value);
    };
    const handlePasswordChange = (e) => {
        setPassword(e.target.value);
    };
    var delayInMilliseconds = 5000; //1 second

    const handleClick = () => {
        let error = false;
        const regex =
            /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
        if (regex.test(value) === false) {
            setEmailError("Not A valid Email");
            setTimeout(function() {
                setEmailError("");
            }, delayInMilliseconds);
            error = true;
        }

        if (password.length < 8) {
            setPasswordError("Password should be atleast 8 letters");
            setTimeout(function() {
                setPasswordError("");
            }, delayInMilliseconds);
            error = true;
        }

        if (!error) {
            if (login(value, password)) {
                navigate("/");
            } else {
                setPasswordError("Invalid username or password");
            }
        }
    };
    return (
        <div className="Page  ">
            <h1>Login</h1>
            <input
                type="text"
                name="text"
                onChange={handleChange}
                value={value}
                label="email"
            />
            <input
                type="password"
                name="password"
                onChange={handlePasswordChange}
                value={password}
                label="password"
            />
            <div className="Error">
               {emailError ? <Alert severity="error">{emailError}</Alert> : null}
               {passwordError ? <Alert severity="error">{passwordError}</Alert> : null}
            </div>
            <Button className='Login' variant="contained" href={name} onClick={handleClick}>
                Submit
            </Button>
            <div>
                <a href="SignUp">
                    <div>Dont have an account? SignUp</div>
                </a>
                <a className="a" href="Forgot">
                    Forgot Password
                </a>
            </div>
        </div>
    );
}
